@font-face {
  font-family: Smackover;
  src: local("Smackover"),
       url(Smackover.ttf);
}

input {
  border: 0px;
  border-bottom: 2px solid rgba(190,190,190,0.5);
  background: rgba(90,90,90,0.1);
}

.HaikuLine {
  margin-top: 20px;
}

.match {
  font-family: "Smackover";
  font-size: 3.5em;
  font: Smackover;
  color: rgba(255,255,255,0.9);
  text-align: center;
  text-shadow: 2px 2px rgb(150,150,150);
}

.nomatch {
  font-family: "Smackover";
  font: Smackover;
  color: red;
  font-size: 3.5em;
  text-align: center;
}

.dotimg {
  padding-left: 20px;
}

